FlexboxItem,
Divider,
XInput,
Group,
XButton,
Cell,
XAddress,
Loading,
XHeader,
Confirm
},
data() {
return {
data:{},

type: 0,
tel: '',  // 手机号
curTel: '', //当前手机号加密
curmobile: '', // 当前手机号不加密的
code: '',   // 验证码
swit: false, // 控制保存按钮的颜色，输入符合验证规则则为true
show2: false,  // 显示隐藏验证码
content: '获取验证码',
totalTime: 60,
canClick: true, // 添加canClick
telValue: function (value) {
return {
valid: value.length >= 11 && /^1[34578]\d{9}$/.test(value),
msg: "验证码有误!"
}
},
codeValue: function (value) {
return {
valid: /\d{4}/.test(value) && value.length >= 4,
msg: "验证码有误！"
}
},
redTips: '',
showTip: '', //提示语
showtishi: false,//控制弹窗显示隐藏
showCancel: false
}
},
methods: {
...mapMutations({
updateMyPhone: 'updateMyPhone'
}),
back() {

















},

checkTel(tips) {
var str = '';
if (this.tel == '') {
str = '手机号码不能为空'
} else if (this.tel.length != 11) {
str = '手机号字符长度必须是11位'
} else if (!(/^1[34578]\d{9}$/.test(this.tel))) {
str = '手机号字符格式不正确'
} else {
this.redTips = 2;
return true;
}
if (tips) {




this.showtishi = true;
this.showTip = str
}
this.redTips = 1;
return false
},
checkCode(tips) {
var str = '';
if (this.code == '') {
str = '验证码不能为空'
} else if (this.code.length != 6) {
str = '请输入6位字符串'
} else {
this.redTips = 6;
return true;
}
if (tips) {
this.showtishi = true;
this.showTip = str
}
this.redTips = 3;
return false
},

getCode() {

if (this.tel) {
if (this.tel == this.myInfo.phone) {
this.showtishi = true
this.showTip = "该手机号已经绑定过"
} else {
this.showtishi = false
this.showTip = ""
if (!this.canClick) return

this.getCodeData()
}
} else {
this.showTip = "手机号不能为空"
this.showtishi = true



}
},

getCodeData() {
this.$vux.loading.show({
text: 'Loading'
})
userApis.getValiCode(this.tel.toString()).then(res => {
this.$vux.loading.hide()
if (res.code === 200) {
this.canClick = false
this.content = this.totalTime + 's'
let clock = window.setInterval(() => {
this.totalTime--
this.content = this.totalTime + 's'
if (this.totalTime < 0) {
window.clearInterval(clock)
this.content = '获取验证码'
this.totalTime = 59
this.canClick = true  
}
}, 1000)
this.$vux.toast.text('已发送验证码', 'bottom')
} else {
this.$vux.toast.text(res.message, 'bottom')
}
}).catch(err => {
this.$vux.loading.hide()
this.$vux.toast.text('获取验证码失败', 'bottom')
console.error(err)
})
},

saveInfo() {
if (this.checkTel(true) && this.checkCode(true)) {
this.$vux.loading.show({
text: '绑定中'
})
const bundle = {
phone: this.tel.toString(),
code: this.code
}
userApis.validatePhone(bundle).then(res => {
this.$vux.loading.hide()
if (res.code === 200) {
this.updateMyPhone(bundle.phone)
commonApis.behaveLogs(commonApis.BEHAVIOR_AUTH_MOBILE) 
if (this.type === 0) {

this.$router.push({path: '/voucherDetails'})
} else if (this.type === 1) {
console.log('返回到红包券详情');
this.$router.push({path: '/voucherDetails'})
} else if (this.type === 2) {
console.log('从我的页面进入');
this.$router.push({path: '/coupons'})
} else if (this.type === 3) {
console.log('返回到抵扣券详情');
this.$router.push({path: '/voucherDetails', query:{source :0}})
} else if(this.type === 4){
console.log('返回到送祝福页面');
this.$router.push('/blessing')
} else if (this.type === 7) {
console.log('返回到我的页面');
this.$router.push({path: '/mys'});
}
} else {
this.$vux.toast.text(res.message, 'bottom')
}
})
}
},


validRule(num) {
if (this.checkTel() && this.checkCode()) {
this.swit = true
} else {
this.swit = false
}
}
},
mounted() {
const { type, data } = this.$route.query
this.type = parseInt(type);
this.data = data;
},
computed: {
...mapState({
myInfo: state => state.user.myInfo
})
}
}
